<template>
    <div class="container">
        <div class="main__users" v-if="todos">
            <div class="main__users-item" v-for="todo in todos" :key="todo.id">
                <p class="first__text">{{ todo.id }}</p>
                <p>{{ todo.title }}</p>
            </div>
        </div>
        <div v-else>
            Идет загрузка...
        </div>
    </div>
</template>

<script>
    export default {
       data() {
            return {
                
            }
       },
       created() {
        this.getTodos()
       },
       computed:{
        todos(){
            return this.$store.state.todos
        }
       },
       methods: {
        getTodos(){
            this.$store.dispatch('getTodos')
        }  
       },
    }
</script>